* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  -webkit-tap-highlight-color: transparent;
  user-select: none;
}

.app {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.game-container {
  width: 100%;
  height: 100%;
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 500px;
  margin: 0 auto;
}

.score-board {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  color: white;
  font-size: 24px;
  font-weight: bold;
  z-index: 10;
}

.score, .target {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.court {
  flex: 1;
  position: relative;
  background: linear-gradient(to bottom, #87CEEB 0%, #98D8C8 50%, #F7DC6F 100%);
  overflow: hidden;
  width: 100%;
}

.basket {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translateX(-50%);
  z-index: 5;
}

.hoop {
  width: 120px;
  height: 120px;
  border: 8px solid #8B4513;
  border-radius: 50%;
  position: relative;
  background: transparent;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.hoop::before {
  content: '';
  position: absolute;
  top: -4px;
  left: -4px;
  right: -4px;
  bottom: -4px;
  border: 4px solid #654321;
  border-radius: 50%;
}

.net {
  position: absolute;
  top: 8px;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 60px;
  border-left: 2px solid rgba(255, 255, 255, 0.6);
  border-right: 2px solid rgba(255, 255, 255, 0.6);
  border-bottom: 2px solid rgba(255, 255, 255, 0.6);
  border-radius: 0 0 50% 50%;
  background: linear-gradient(to bottom, transparent 0%, rgba(255, 255, 255, 0.3) 100%);
}

.basketball {
  position: absolute;
  width: 80px;
  height: 80px;
  font-size: 60px;
  cursor: pointer;
  transition: transform 0.1s ease;
  z-index: 10;
  touch-action: none;
  display: flex;
  align-items: center;
  justify-content: center;
}

.basketball.dragging {
  transform: translate(-50%, -50%) scale(1.1);
  filter: brightness(1.2);
}

.basketball:active {
  transform: translate(-50%, -50%) scale(1.1);
}

.trajectory {
  position: absolute;
  font-size: 40px;
  color: rgba(255, 255, 255, 0.7);
  z-index: 8;
  pointer-events: none;
  transform-origin: center;
}

.points-popup {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translateX(-50%);
  font-size: 48px;
  font-weight: bold;
  color: #FFD700;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.5);
  z-index: 20;
  animation: popup 1.5s ease-out forwards;
  pointer-events: none;
}

@keyframes popup {
  0% {
    opacity: 0;
    transform: translateX(-50%) translateY(0) scale(0.5);
  }
  50% {
    opacity: 1;
    transform: translateX(-50%) translateY(-30px) scale(1.2);
  }
  100% {
    opacity: 0;
    transform: translateX(-50%) translateY(-60px) scale(1);
  }
}

.game-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 100;
}

.game-message {
  background: white;
  padding: 40px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
}

.game-message h2 {
  font-size: 36px;
  margin-bottom: 20px;
  color: #667eea;
}

.game-message button {
  padding: 15px 30px;
  font-size: 18px;
  background: #667eea;
  color: white;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease;
}

.game-message button:hover {
  background: #5568d3;
}

.game-message button:active {
  transform: scale(0.95);
}

.instructions {
  padding: 15px;
  text-align: center;
  color: white;
  font-size: 16px;
  background: rgba(0, 0, 0, 0.2);
  backdrop-filter: blur(5px);
}

/* Mobile optimizations */
@media (max-width: 480px) {
  .score-board {
    font-size: 20px;
    padding: 15px;
  }

  .basketball {
    width: 70px;
    height: 70px;
    font-size: 50px;
  }

  .hoop {
    width: 100px;
    height: 100px;
  }

  .net {
    width: 85px;
    height: 50px;
  }

  .points-popup {
    font-size: 40px;
  }

  .game-message {
    padding: 30px;
    margin: 20px;
  }

  .game-message h2 {
    font-size: 28px;
  }
}

/* Prevent text selection and context menu on mobile */
.basketball,
.court {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-touch-callout: none;
}

/* Win Image */
.win-image-container {
  width: 100%;
  max-width: 300px;
  margin: 0 auto 20px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.win-image {
  width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
}

.game-message p {
  margin-top: 10px;
  font-size: 16px;
  color: #666;
}

/* Level 2 Styles */
.level2-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.level2-title {
  font-size: 42px;
  margin-bottom: 40px;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.team-options {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  max-width: 400px;
  margin-bottom: 30px;
}

.team-button {
  padding: 20px 30px;
  font-size: 24px;
  font-weight: bold;
  background: white;
  color: #667eea;
  border: 3px solid white;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-align: center;
  min-height: 70px;
}

.team-button:hover:not(:disabled) {
  background: #f0f0f0;
  transform: scale(1.05);
}

.team-button:active:not(:disabled) {
  transform: scale(0.98);
}

.team-button:disabled {
  cursor: not-allowed;
  opacity: 0.7;
}

.team-button.selected.wrong {
  background: #ff4444;
  color: white;
  border-color: #ff4444;
  animation: shake 0.5s;
}

.team-button.shaking {
  animation: shake 0.5s;
}

@keyframes shake {
  0%, 100% { transform: translateX(0); }
  10%, 30%, 50%, 70%, 90% { transform: translateX(-10px); }
  20%, 40%, 60%, 80% { transform: translateX(10px); }
}

.wrong-answer-message {
  font-size: 28px;
  color: #ff4444;
  font-weight: bold;
  text-align: center;
  margin-top: 20px;
  animation: fadeInOut 2s ease-in-out;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

@keyframes fadeInOut {
  0%, 100% { opacity: 0; transform: translateY(-10px); }
  50% { opacity: 1; transform: translateY(0); }
}

.correct-answer-message {
  text-align: center;
  margin-top: 30px;
  animation: slideIn 0.5s ease-out;
}

.correct-answer-message h2 {
  font-size: 32px;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  color: #FFD700;
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.play-again-button {
  padding: 15px 40px;
  font-size: 20px;
  background: white;
  color: #667eea;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.play-again-button:hover {
  background: #f0f0f0;
  transform: scale(1.05);
}

.play-again-button:active {
  transform: scale(0.95);
}

/* Mobile optimizations for Level 2 */
@media (max-width: 480px) {
  .level2-title {
    font-size: 32px;
    margin-bottom: 30px;
  }

  .team-button {
    font-size: 20px;
    padding: 18px 25px;
    min-height: 60px;
  }

  .wrong-answer-message {
    font-size: 24px;
  }

  .correct-answer-message h2 {
    font-size: 26px;
  }

  .play-again-button {
    font-size: 18px;
    padding: 12px 30px;
  }

  .win-image-container {
    max-width: 250px;
  }
}

/* Level 3 Styles */
.level3-container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
  overflow-y: auto;
}

.level3-title {
  font-size: 36px;
  margin-bottom: 10px;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.level3-instruction {
  font-size: 18px;
  margin-bottom: 30px;
  text-align: center;
  opacity: 0.9;
}

.level3-items-container {
  display: flex;
  flex-direction: column;
  gap: 15px;
  width: 100%;
  max-width: 500px;
  margin-bottom: 30px;
}

.level3-item {
  display: flex;
  align-items: center;
  gap: 15px;
  padding: 15px;
  background: white;
  color: #667eea;
  border-radius: 15px;
  cursor: move;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  position: relative;
  min-height: 100px;
}

.level3-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.3);
}

.level3-item.dragging {
  opacity: 0.5;
  transform: scale(0.95);
}

.level3-item.drag-over {
  border: 3px dashed #667eea;
  background: #f0f0ff;
}

.level3-item.correct {
  background: #4caf50;
  color: white;
  animation: correctPulse 0.5s ease;
}

@keyframes correctPulse {
  0%, 100% { transform: scale(1); }
  50% { transform: scale(1.05); }
}

.level3-item-image {
  width: 80px;
  height: 80px;
  border-radius: 10px;
  overflow: hidden;
  background: #f5f5f5;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
}

.level3-item-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 40px;
  background: linear-gradient(135deg, #e0e0e0 0%, #f5f5f5 100%);
}

.level3-item-name {
  flex: 1;
  font-size: 20px;
  font-weight: bold;
  text-align: left;
}

.level3-item-number {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: #667eea;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  font-weight: bold;
  flex-shrink: 0;
}

.level3-item.correct .level3-item-number {
  background: white;
  color: #4caf50;
}

.level3-complete-message {
  text-align: center;
  margin-top: 20px;
  animation: slideIn 0.5s ease-out;
}

.level3-complete-message h2 {
  font-size: 32px;
  margin-bottom: 30px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  color: #FFD700;
}

/* Mobile optimizations for Level 3 */
@media (max-width: 480px) {
  .level3-title {
    font-size: 28px;
  }

  .level3-instruction {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .level3-item {
    padding: 12px;
    min-height: 80px;
    gap: 12px;
  }

  .level3-item-image {
    width: 60px;
    height: 60px;
  }

  .level3-item-name {
    font-size: 18px;
  }

  .level3-item-number {
    width: 35px;
    height: 35px;
    font-size: 16px;
  }

  .level3-complete-message h2 {
    font-size: 26px;
  }
}

