  margin-bottom: 30px;
  text-align: center;
  opacity: 0.9;
  font-style: italic;
}

.wordle-board {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 30px;
}

.wordle-row {
  display: flex;
  gap: 8px;
  justify-content: center;
}

.wordle-cell {
  width: 60px;
  height: 60px;
  border: 3px solid #fff;
  background: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 28px;
  font-weight: bold;
  color: white;
  text-transform: uppercase;
  transition: all 0.3s ease;
  border-radius: 5px;
}

.wordle-cell.correct {
  background: #4caf50;
  border-color: #4caf50;
  animation: flip 0.5s ease;
}

.wordle-cell.present {
  background: #ff9800;
  border-color: #ff9800;
  animation: flip 0.5s ease;
}

.wordle-cell.absent {
  background: #666;
  border-color: #666;
  animation: flip 0.5s ease;
}

@keyframes flip {
  0% {
    transform: rotateX(0);
  }
  50% {
    transform: rotateX(90deg);
  }
  100% {
    transform: rotateX(0);
  }
}

.wordle-complete-message {
  text-align: center;
  margin-top: 20px;
  animation: slideIn 0.5s ease-out;
}

.wordle-complete-message h2 {
  font-size: 32px;
  margin-bottom: 15px;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  color: #FFD700;
}

.wordle-complete-message p {
  font-size: 20px;
  margin-bottom: 10px;
  opacity: 0.9;
}

/* Mobile optimizations for Wordle */
@media (max-width: 480px) {
  .wordle-title {
    font-size: 36px;
  }

  .wordle-hint {
    font-size: 16px;
    margin-bottom: 20px;
  }

  .wordle-cell {
    width: 50px;
    height: 50px;
    font-size: 24px;
  }

  .wordle-complete-message h2 {
    font-size: 26px;
  }

  .wordle-complete-message p {
    font-size: 18px;
  }
}

/* Entry/Landing Screen Styles */
.entry-container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: white;
}

.entry-image-container {
  width: 100%;
  max-width: 400px;
  height: 400px;
  margin-bottom: 30px;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.3);
  background: rgba(255, 255, 255, 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
}

.entry-image {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.entry-image-placeholder {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 120px;
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2) 0%, rgba(255, 255, 255, 0.1) 100%);
}

.entry-title {
  font-size: 48px;
  margin-bottom: 10px;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
  font-weight: bold;
}

.entry-subtitle {
  font-size: 24px;
  margin-bottom: 40px;
  text-align: center;
  opacity: 0.9;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.start-game-button {
  padding: 20px 50px;
  font-size: 24px;
  font-weight: bold;
  background: white;
  color: #667eea;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
  text-transform: uppercase;
  letter-spacing: 2px;
}

.start-game-button:hover {
  background: #f0f0f0;
  transform: scale(1.05);
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4);
}

.start-game-button:active {
  transform: scale(0.98);
}

/* Mobile optimizations for Entry Screen */
@media (max-width: 480px) {
  .entry-image-container {
    max-width: 300px;
    height: 200px;
    margin-bottom: 20px;
  }

  .entry-image-placeholder {
    font-size: 80px;
  }

  .entry-title {
    font-size: 36px;
  }

  .entry-subtitle {
    font-size: 20px;
    margin-bottom: 30px;
  }

  .start-game-button {
    padding: 18px 40px;
    font-size: 20px;
  }
}

